<template>
    <div class="yingyong">
        <headerBar :title="headerNavData"></headerBar>
        <div class="yd-grids-4">
            <div class="yd-grids-item" v-for="item in list" :id="item.id" @click="godetail(item)">
                <div class="yd-grids-icon">
                  <img :src="item.iconPath">
                </div>
                <div class="yd-grids-txt">
                  <span>{{item.appName}}</span>
                  <span>{{item.appEngName}}</span>
                </div>
            </div>
        </div>
        <norecord :nodata="nodata" v-if="shownorecord"></norecord>
        <!-- 尾部 -->
        <footerBar :footerdata="footerdata"></footerBar>
    </div>
  
</template>

<script>
  import headerBar from './header.vue'
  import footerBar from './footer.vue'
  import norecord from './norecord.vue'
  import { encrypt } from '../assets/js/Keytoken.js'
export default {
  components:{
      "headerBar":headerBar,
      "footerBar":footerBar,
      "norecord":norecord
  },
  data () {
    return {
      footerdata:{
        bj:'#fdfdff'
      },
      headerNavData:{
         left:false,
         title:this.$t('header.yingyong'),
         right:true,
         bj:'#2c94ed',
         search:true,
         share:false
      },
      html:'',
      shownorecord:false,
      nodata:this.$t('toast.creating'),
      list:[]
    }
  },
  created(){
    this.yingyong()
  },
  methods:{
    yingyong(){
      var that = this
      this.axios({
        url:this.Url + 'App.ashx',
        method:'post',
        params: {req:4}
      }).then(res=>{
        var _list = res.data.data
        if (res.data.success) {
          if (res.data.msg == "没有数据") {that.shownorecord = true}
          for (var i = 0; i < _list.length; i++) {
            _list[i].iconPath = this.imgUrl + _list[i].iconPath
          }
          that.list = _list
        }
      }).catch(function(res){
          that.closeloading()
          res = JSON.stringify(res)
          that.$refs.pullrefreshDemo.$emit('ydui.pullrefresh.finishLoad');
          if(JSON.stringify(JSON.parse(res).request)=="{}"){
              that.catchErr()
              setTimeout(function(){
                  that.$dialog.confirm({
                      title: '网络不给力请重新加载...',
                      opts: (res) => {
                          that.loadList(2) 
                      }
                  });
              },2000)
          }
      });
    },
    godetail(item){
      localStorage.setItem("toAddress",item.rechargeAddress)
      setTimeout(function(){
        this.$router.push({path:"/toup"})
      }.bind(this),200)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.yd-grids-item span:nth-child(2){
  display: block;
  color: #888;
  font-size:.2rem;
}
.yd-grids-icon img{
  height: 100%;
}
.yingyong{
  padding-top: 1.2rem;
  font-family: -apple-system, BlinkMacSystemFont, "PingFang SC","Helvetica Neue",STHeiti,"Microsoft Yahei",Arial,Tahoma,Simsun,sans-serif;
  -webkit-touch-callout:none;
    -webkit-user-select:none;
    -khtml-user-select:none;
    -moz-user-select:none;
    -ms-user-select:none;
    user-select:none;
}
.yd-grids-item:before{
    border-right: 0px solid #d9d9d9!important
}
.yd-grids-item:after{
  border-bottom:0px solid #d9d9d9!important;
}
.yd-grids-txt{
  padding: .1rem 0
}
.yd-grids-icon img{
  width: .8rem;
  height: .8rem;
}
</style>



